// Typography Settings ------------------------------------
$fonts: Roboto, Arial, sans-serif, "", "Microsoft JhengHei", "";
$fz: 14px;

body {
	font: {
		family: $fonts;
		size: $fz; 
	}
	vertical-align: baseline;
	h1, h2, h3, h4, h5, h6 {font-weight: bold;}
	h1 {font-size: $fz + 14px;}
	h2 {font-size: $fz + 12px;}
	h3 {font-size: $fz + 10px;}
	h4 {font-size: $fz + 6px;}
	h5 {font-size: $fz + 4px;}
	h6 {font-size: $fz + 2px;}
}
@include establish-baseline;

